import MetaInfo from './components/meta-info/index'

import WLoadingBar from './components/loading-bar/index'
import Skeleton from './components/skeleton/index'

import QiuButton from './components/qiu-button/index';

import QiuIcon from './components/qiu-icon/index';
import QiuTag from './components/qiu-tag/index';

import QiuRow from './components/qiu-row/index';
import QiuCol from './components/qiu-col/index';

import QiuLayout from './components/qiu-layout/index';
import QiuContent from './components/qiu-layout/qiu-content/index';
import QiuHeader from './components/qiu-layout/qiu-header/index';
import QiuSider from './components/qiu-layout/qiu-sider/index';
import QiuFooter from './components/qiu-layout/qiu-footer/index';

//import  QiuMenu from './components/qiu-menu/index';
//import QiuMenuItem  from './components/qiu-menu/qiu-menu-item/index';
import QiuMenu from './components/qiu-menu/index';
import QiuSubMenu from './components/qiu-menu/qiu-submenu/index';
import QiuItemMenu from './components/qiu-menu/qiu-item-menu/index';
import QiuMenuGroup from './components/qiu-menu/qiu-menu-group/index';

import QiuInput from './components/qiu-input/index';
import QiuSwitch from './components/qiu-switch/index';

const components = [
  Skeleton,
  QiuButton,
  QiuIcon,
  QiuTag,
  QiuRow,
  QiuCol,
  QiuContent,
  QiuHeader,
  QiuSider,
  QiuFooter,
  QiuLayout,
  QiuMenu, 
  QiuSubMenu,
  QiuItemMenu,
  QiuMenuGroup,
  QiuInput,
  QiuSwitch
]

const install = function(Vue) {
  if (install.installed) return
  components.map(component => Vue.component(component.name, component))
  MetaInfo.install(Vue)
  Vue.prototype.$loading = WLoadingBar
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  QiuButton
}



