<template>
  <span
    class="qiu-switch"
    :class="[
      size ? `qiu-switch--${size}` : '',
      {
        'qiu-switch--disabled': disabled,
        'qiu-switch--checked': checkStatus
      }
    ]"
    @click="toggleSwitch">
    <span class="qiu-switch__text">
      <slot v-if="checkStatus" name="checkedText"></slot>
      <slot v-if="!checkStatus" name="unCheckedText"></slot>
    </span>
  </span>
</template>

<script>
export default {
  name: 'QiuSwitch',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    size: String
  },
  data () {
    return {
      checkStatus: this.value
    }
  },
  methods: {
    toggleSwitch () {
      if (this.disabled) return
      this.checkStatus = !this.checkStatus
      this.$emit('change', this.checkStatus)
    }
  }
}
</script>


