<template>
  <ul class="qiu-menu__item-group">
    <li class="qiu-menu__item-group-title">{{ title }}</li>
    <ul class="qiu-menu__item-group-list"><slot></slot></ul>
  </ul>
</template>

<script>
  export default {
    name: 'QiuMenuItemGroup',
    props: {
      title: {
        type: String,
        default: ''
      }
    }
  }
</script>


